	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Flying away for the weekend","date":"28 Nov 2000","slug":"/2000-11-28-flying-away-for-the-weekend/","tags":"Y2000"},"html":"<p>We'll be in Munich this weekend, visiting a friend of Mama from work. Mama and Christopher are flying for free (she has flyer miles, he's too young for a ticket). I get to pay full price.</p>\n<p>Christopher got a clean bill of health from his pediatrician yesterday, so he'll be going today to his first day of \"nursery school\" (that's what his Grandpa calls the <i>Tagesmutter</i>; it's really just day care). He'll stay just 2 hours today. It's just a practice session.</p>\n<p>Mama still doesn't know what they will be wanting her to do at work when she goes back on January 1. She says that her gut feeling is they have big plans for her right away. And we thought we could just ease into 2-career parenting... we might have to jump in feet first with our eyes closed.</p>\n<p>How about if we award the election to the first one who starts behaving like a president and a statesman, you know, with honor and dignity and all that. But then again, maybe neither one would win...</p>"},"next":{"fields":{"title":"Never leave your child unattended....","date":"24 Nov 2000","slug":"/2000-11-24-never-leave-your-child-unattended/"}},"previous":{"fields":{"title":"Wir sind das Netz!","date":"30 Nov 2000","slug":"/2000-11-30-wir-sind-das-netz/"}}},{"node":{"fields":{"title":"Never leave your child unattended....","date":"24 Nov 2000","slug":"/2000-11-24-never-leave-your-child-unattended/","tags":"Y2000"},"html":"<p><em><strong>... not even for a moment! </strong></em></p>\n<p><em><strong><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/11/dishwasher.jpg\" height=\"337\" width=\"450\" border=\"0\" alt=\"dishwasher: \" /></strong></em></p>\n<p><em><strong><br />I'm guilty. I ran upstairs to get something - no, I did not check up on my stocks. When I returned, Christopher was sitting on the dishwasher door. I should be happy our son takes such a keen interest in household appliances. Before we know it he might volunteer to unload the dishes. Luckily the camera was right there on the kitchen counter. Obviously, the ear infection doesn't seem to bother him at all. Looks like we can start the nursery school practice sessions on Monday.</strong></em> </p>\n<hr />\n<p>My caption for the picture would have been: Sound advice for frugal parents: wash your child and your dishes at the same time!</p>"},"next":{"fields":{"title":"Not again","date":"22 Nov 2000","slug":"/2000-11-22-not-again/"}},"previous":{"fields":{"title":"Flying away for the weekend","date":"28 Nov 2000","slug":"/2000-11-28-flying-away-for-the-weekend/"}}},{"node":{"fields":{"title":"Not again","date":"22 Nov 2000","slug":"/2000-11-22-not-again/","tags":"Y2000"},"html":"<p>After a high fever over the weekend, we thought Christopher was over it on Monday. But he isn't. He's got an ear infection, so it's back on antibiotics, and no play groups for a week. But he doesn't look or act sick, and his appetite is good.</p>\n<p>Tomorrow is just another work day for me, but Christopher will be celebrating with a toddler-sized jar of <a href=\"http://www.hipp.de\">Hipp</a> turkey and vegetables!</p>\n<p>In our excitement of staying-home-while-Mom's-on-a-business-trip, we forgot to put out the trash on Monday night. That's bad. Our only trashbin is plump full of diapers, and the next pick-up is in two weeks.</p>"},"next":{"fields":{"title":"Short and snappy","date":"21 Nov 2000","slug":"/2000-11-21-short-and-snappy/"}},"previous":{"fields":{"title":"Never leave your child unattended....","date":"24 Nov 2000","slug":"/2000-11-24-never-leave-your-child-unattended/"}}}],"pathPrefix":"","first":false,"last":false,"index":1033,"pageCount":1101}}

